
.nav {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  margin: 0 auto;
  width: 90%;
  color: #383838;
}
.nav .nav-left .logo {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    position: relative;
    height: 80px;
    line-height: 80px;
}
.nav .nav-left .logo img {
      display: inline-block;
      padding-top: 15px;
      height: 40px;
}
.nav .nav-left .logo span {
      display: inline-block;
      margin-left: 10px;
      color: #3da8f5;
      font-weight: bold;
      font-size: 16px;
      line-height: 90px;
}
.nav .nav-right .navbar {
    display: inline-block;
    margin: 0;
    padding: 0;
    height: 80px;
    font-size: 14px;
    line-height: 80px;
    list-style-type: none;
}
.nav .nav-right .navbar li {
      position: relative;
      display: inline-block;
      cursor: pointer;
      -webkit-transition: color .3s;
      transition: color .3s;
}
.nav .nav-right .navbar li a {
        display: block;
        padding: 0 24px;
        color: #383838;
        -webkit-transition: color .3s;
        transition: color .3s;
}
.nav .nav-right .navbar li a:hover {
          color: #3da8f5;
}
.nav .nav-right .navbar li a.router-activity {
          color: #3da8f5;
}
.nav .nav-right .navbar li a.router-activity::after {
            content: '';
            position: absolute;
            bottom: 0;
            left: 0;
            height: 2px;
            width: 100%;
            background-color: #3da8f5;
}



